<template>
  <div>
    <el-drawer title="我是标题" :visible.sync="drawer" :with-header="false">
      <div style="padding: 20px">
        <div class="pagebar-title">选择网关所属主体</div>

        <div class="module filter" style="margin-top: 25px">
          <div class="l">
            <el-input placeholder="请输入内容" v-model="form.keyword">
              <template slot="prepend">搜索主体</template>
            </el-input>
          </div>
        </div>

        <div class="module">
          <el-button @click="getList()">搜索</el-button>
        </div>

        <div class="module">
          <el-table :data="sel.tableData" style="width: 100%">
            <el-table-column prop="id" label="系统ID" width="100">
            </el-table-column>

            <el-table-column prop="name" label="主体名"> </el-table-column>

            <el-table-column label="更多" fixed="right" width="100">
              <template slot-scope="scope">
                <el-button @click="sel_principal(scope.row.id, scope.row.name)">选择</el-button>
              </template>
            </el-table-column>

          </el-table>

        </div>

        <div class="module">
          <Pagination
            @pagination="getList()"
            :page.sync="form.querypage"
            :pageCount.sync="sel.totalPage"
            :total.sync="sel.total"
            v-if="sel.total > 0"
          />
        </div>
      </div>
    </el-drawer>

    <div class="pagebar-title">新增网关设备</div>

    <div class="module">
      <div class="import" v-if="!form.principal_id">
        <div class="txt">设置选择网关设备主体</div>
        <div class="r">
          <el-button @click="drawer = true">选择主体</el-button>
        </div>
      </div>
      <div class="import" v-if="form.principal_id">
        <div class="txt">当前选择: {{ form.principal_name }}</div>
        <div class="r">
          <el-button @click="drawer = true">重新选择</el-button>
        </div>
      </div>
    </div>

    <div class="module">
      <div class="mtitle">设备名</div>
      <el-input v-model="form.name" placeholder="必填"></el-input>
    </div>

    <div class="module">
      <el-button @click="submit()">添加</el-button>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { AddorEdit } from "@/api/gateway";
import { Query } from "@/api/principal";
import Pagination from "@/components/Pagination.vue";
export default {
  name: "gatewayAdd",
  data() {
    return {
      drawer: false,
      form: {
        id: null,
        name: null,
        principal_id: null,
        principal_name: null,
      },
      sel: {
        form: {
          keyword: "",
          querypage: 1,
        },
        tableData: [], // 列表
        total: 0, // 总条目
        totalPage: 0, // 总页数
      },
    };
  },
  components: {
    Pagination,
  },
  methods: {
    sel_principal(id,name) {
      this.drawer = false
      this.form.principal_id = id
      this.form.principal_name = name
    },
    submit() {
      this.$http(AddorEdit(this.form), (res) => {
        console.log(res);
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            type: "success",
          });
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    },
    getList() {
      this.$http(Query(this.sel.form), (res) => {
        console.log(res);
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            type: "success",
          });
          this.sel.total = res.data.total;
          this.sel.form.querypage = res.data.currentPage;
          this.sel.totalPage = res.data.totalPages;
          this.sel.tableData = res.data.result;
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    },
  },
};
</script>
<style lang="scss" scoped>
</style>